.investors__wrapper-top {
  min-height: 670px;
  padding-top: 178px;
  padding-bottom: 150px;
  background: url("../img/road2.png");
  background-position: 0 0;
  background-size: 45% 100%;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  background-color: #f9f9f9;
  margin-bottom: 100px;

  @media screen and (max-width: @extra-large) {
    flex-direction: column;
    background-size: 100% 60%;
  }

  @media screen and (max-width: @large) {
    margin-bottom: 40px;
    padding-bottom: 50px;
  }
}

.investors__title-wrapper {
  width: 100%;
  max-width: 320px;
  margin-right: 60px;
  margin-left: auto;

  @media screen and (max-width: @extra-large) {
    max-width: 560px;
    margin-left: 11%;
    margin-right: auto;
    margin-bottom: 50px;
  }

  @media screen and (max-width: @medium) {
    max-width: 460px;
  }

  @media screen and (max-width: @small) {
    margin-right: auto;
    margin-left: auto;
    max-width: 80%;
  }
}

.investors__txt-line {
  border-top: 2px solid @white;
  text-align: right;
  color: @white;

  span {
    color: @black;
    background-color: @white;
    padding: 10px 7px;
    border-radius: 50%;
  }
}

.investors__title {
  color: @white;
  margin-bottom: 40px;
}

.investors__diagramma-wrapper {
  width: 100%;
  max-width: 762px;
  margin-right: auto;
  display: flex;
  background-color: @white;
  padding: 52px;
  padding-top: 72px;

  @media screen and (max-width: @extra-large) {
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    align-items: center;
  }
}

.investors__diagramma-images {
  height: 340px;
  width: 362px;
  display: flex;
  flex-direction: column;
}

.investors__diagramma-txt {
  width: 100%;
  max-width: 240px;
}

.investors__diagramma-img {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.investors__diagramma-slogan {
  font-size: 17px;
  font-weight: bold;
  margin-bottom: 40px;
}

.investors__diagramma-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.investors__diagramma-list-item {
  width: 45%;
  margin-bottom: 40px;

  &:nth-child(4),
  &:nth-child(3) {
    margin-bottom: 0;
  }
}

.investors__wrapper-middle {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 100px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 62px;
    width: 56px;
    position: absolute;
    bottom: 0;
    right: 40px;
    z-index: 0;
    transform: translateY(50%);
    background-color: @white;
    background-image: url("../img/arrow.png");
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
  }

  @media screen and (max-width: @extra-large) {
    flex-direction: column;
    align-items: center;
  }

  @media screen and (max-width: @large) {
    padding-bottom: 40px;
  }
}

.investors__bold-big {
  display: block;
  width: 100%;
  font-size: 40px;
  font-weight: bold;
  line-height: 1;
  margin-bottom: 30px;

  @media screen and (max-width: @large) {
    text-align: center;
  }
}

.investors__wrapper-middle-left,
.investors__wrapper-middle-right {
  width: 100%;
  max-width: 460px;

  @media screen and (max-width: @large) {
    margin-bottom: 40px;
  }
}

.investors__wrapper-middle-right {
  border-bottom: 2px solid @black;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 30px;
    width: 30px;
    position: absolute;
    bottom: 0;
    right: 40px;
    z-index: 0;
    transform: translateY(-50%);
    background-color: @white;
    background-image: url("../img/tick.png");
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
  }

  @media screen and (max-width: @large) {
    padding-bottom: 40px;
  }
}
