.numbers {
  padding-top: 128px;
  padding-bottom: 150px;
  background: url("../img/road.png");
  background-position: 0 30%;
  background-size: 45% 30%;
  background-repeat: no-repeat;

  @media screen and (max-width: @small) {
    background-position: 0 17%;
    background-size: 90% 330px;
    padding-bottom: 40px;
  }
}

.numbers__top-wrapper {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }

  .numbers__top-wrapper-inner {
    width: 50%;

    @media screen and (max-width: @small) {
      width: 100%;
    }

    &:first-child {
      padding-left: 90px;

      @media screen and (max-width: @large) {
        padding-left: 0;
        padding-right: 10px;
      }

      @media screen and (max-width: @small) {
        padding-bottom: 400px;
      }
    }

    .numbers__list {
      margin-top: 30px;
    }

    @media screen and (max-width: @large) {
      max-width: 350px;
    }

    @media screen and (max-width: @small) {
      max-width: 100%;
    }
  }
}

.number__txt {
  width: 400px;

  @media screen and (max-width: @large) {
    width: 100%;
  }
}

.numbers__list {
  border-right: 1px solid #b1b1b1;

  @media screen and (max-width: @small) {
    border: none;
  }
}

.numbers__list-item {
  margin-bottom: 20px;
  border-bottom: 1px solid #b1b1b1;
  padding-bottom: 50px;

  @media screen and (max-width: @small) {
    border: none;
  }
}

.numbers__list-txt {
  padding-right: 20px;
}

.numbers__shadow-text {
  font-size: 100px;
  font-family: @rubik;
  font-weight: bold;
  font-style: italic;
  -webkit-text-stroke: .01em black;
  line-height: 1;
  color: @white;
  margin-bottom: 15px;

  @media screen and (max-width: @large) {
    font-size: 90px;
  }
}

.numbers__red {
  font-size: 40px;
  font-weight: bold;
  color: @red;
  -webkit-text-stroke: 0;
}

.numbers__bottom-wrapper {
  padding-top: 232px;
  display: flex;
  // justify-content: space-between;
  align-items: center;
  background: url("../img/text.png");
  background-position: 0 30%;
  background-size: 100% 94px;
  background-repeat: no-repeat;

  @media screen and (max-width: @extra-large) {
    flex-direction: column-reverse;
  }

  @media screen and (max-width: @small) {
    padding-top: 50px;
    background-size: 100% 34px;
  }

  .numbers__wrapper-inner {
    &:first-child {
      @media screen and (max-width: @small) {
        margin: 0;
      }
    }

    &:last-child {
      @media screen and (max-width: @small) {
        width: 100%;
      }
    }
  }
}

.numbers__wrapper-inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 500px;
  position: relative;

  @media screen and (max-width: @laptop) {
    max-width: 400px;
  }

  @media screen and (max-width: @extra-large) {
    max-width: 100%;
    margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;
  }

  &:first-child {
    margin-right: 200px;

    @media screen and (max-width: @extra-large) {
      margin-right: 100px;
    }
  }

  &:nth-child(2) {

  }
}

.numbers__txt {
  width: 40%;

  @media screen and (max-width: @small) {
    width: 100%;
  }
}

.numbers__txt-bold {
  display: block;
  font-size: 17px;
  margin-bottom: 15px;
}

.numbers__btn {
  height: 46px;
  font-weight: 500;
  padding-left: 34px;
  line-height: 17px;
  margin-top: 30px;
}

.numbers__red-element {
  width: 384px;
  height: 532px;
  background-color: @red;

  @media screen and (max-width: @medium) {
    height: 432px;
  }

  @media screen and (max-width: @small) {
    width: 272px;
    max-width: 350px;
    height: 342px;
  }
}

.numbers__img {
  position: absolute;
  bottom: 0;
  margin-left: -15%;

  @media screen and (max-width: @medium) {
    height: 322px;
    width: 500px;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    height: auto;
    margin-left: 0;
  }
}
