.pop-up {
  display: none;
  padding: 40px;
  width: 100%;
  max-width: 560px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: @white;
  z-index: 10;

  @media screen and (max-width: @small) {
    padding: 20px;
    max-width: 316px;
  }

  &::after {
    content: "";
    display: block;
    height: 139px;
    width: 138px;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 0;
    transform: translate(-50%, 70%);
    background-image: url("../img/red-logo.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }
}

.pop-up__bold {
  font-size: 40px;
  line-height: 1;
  display: block;
  margin-bottom: 25px;
  text-align: center;
}

.pop-up__txt {
  text-align: center;
  margin-bottom: 30px;
}

.pop-up__close-btn {
  height: 29px;
  width: 30px;
  position: absolute;
  top: 12px;
  right: 15px;
  background-color: transparent;
  border: none;
  outline: none;

  &::after {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @black;
    position: absolute;
    top: 13px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @black;
    position: absolute;
    bottom: 13px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-image: url("../img/over-bg.png");
}
