.footer {
  background-color: #f9f9f9;
}

.footer__wrapper {
  padding-top: 70px;
  padding-left: 70px;
  padding-bottom: 70px;
  padding-right: 60px;
  background-color: @white;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media screen and (max-width: @extra-large) {
    flex-direction: column;
    align-items: center;
  }

  @media screen and (max-width: @small) {
    padding: 30px;
    padding-top: 72px;
  }
}

.footer__left-column {
  width: 100%;
  max-width: 45%;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 139px;
    width: 138px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 0;
    transform: translate(50%, -70%);
    background-image: url("../img/red-logo.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;

    @media screen and (max-width: @extra-large) {
      top: 0;
    }

    @media screen and (max-width: @extra-large) {
      transform: translate(0%, -70%);
    }

    @media screen and (max-width: @small) {
      transform: translate(0%, -100%);
    }
  }

  @media screen and (max-width: @large) {
    max-width: 100%;
  }
}

.footer__form {
  width: 100%;
  max-width: 45%;

  @media screen and (max-width: @extra-large) {
    max-width: 45%;
    margin-top: 40px;
  }

  @media screen and (max-width: @large) {
    max-width: 100%;
  }
}

.footer__bold {
  font-size: 40px;
  line-height: 1;
  display: block;
  margin-bottom: 25px;
}

.footer__txt {
  font-size: 17px;
  line-height: 26px;
  margin-bottom: 80px;
}

.footer__contacts-list {
  display: flex;
  justify-content: space-between;
  border-top: 2px solid @black;
  padding-top: 15px;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.footer__contacts-list-item {
  span {
    display: block;
  }

  a {
    font-size: 17px;
    font-weight: bold;
    color: @black;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.footer__bottom {
  width: 100%;
  background-color: #f9f9f9;
  display: flex;
  justify-content: space-between;
  padding: 32px 0;

  @media screen and (max-width: @small) {
    flex-direction: column;
    align-items: center;
  }
}

.footer__protection {
  margin-left: 30%;

  @media screen and (max-width: @small) {
    margin-left: 0;
  }
}

.footer__confidentiality {
  margin-right: 10%;

  @media screen and (max-width: @small) {
    margin-right: 0;
  }
}
