.project-info__wrapper {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @extra-large) {
    flex-direction: column;
    align-items: center;
  }
}

.project-info {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #f9f9f9;
}

.project-info__numer {
  display: block;
  font-size: 30px;
  font-family: @rubik;
  font-weight: 700;
  font-style: italic;
  -webkit-text-stroke: .01em black;
  line-height: 1;
  color: @white;
  margin-bottom: 25px;
  text-align: right;
}

.project-info__bold {
  display: block;
  font-size: 25px;
  line-height: 29px;
  color: @black;
  text-align: right;
  margin-bottom: 25px;
}

.project-info__txt {
  text-align: right;
  margin-bottom: 50px;
}

.project-info__link {
  color: @red;
}

.project-info__left-column,
.project-info__right-column {
  width: 100%;
  max-width: 500px;

  @media screen and (max-width: @large) {
    margin-bottom: 40px;
  }
}

.project-info__left-column {
  .project-info__btn {
    margin-bottom: 100px;
  }
}

.project-info__img-wrapper {
  text-align: center;
}

.project-info__list {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #dedede;
  padding-top: 20px;
  margin-bottom: 50px;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.project-info__list-item {
  @media screen and (max-width: @small) {
    display: flex;
    justify-content: space-between;
  }
}

.project-info__list-item-txt {
  width: 40%;

  @media screen and (max-width: @small) {
    width: 35%;
  }
}

.project-info__list-item-bold {
  display: block;
  font-size: 60px;
  font-family: @rubik;
  line-height: 1;
  font-weight: 700;
  font-style: italic;
  -webkit-text-stroke: .01em @red;
  line-height: 1;
  color: @white;
  margin-bottom: 25px;
  text-align: right;
}

.project-info__btn {
  display: inline-block;
  width: auto;
}

.project-info__main-list {
  margin-bottom: 60px;
}

.project-info__main-list-item {
  padding-left: 130px;
  padding-top: 30px;
  padding-bottom: 20px;
  padding-right: 40px;
  background-color: @white;
  margin-bottom: 30px;
  position: relative;

  @media screen and (max-width: @small) {
    padding-left: 30px;
    padding-top: 100px;
  }

  &::after {
    content: "";
    display: block;
    height: 64px;
    width: 64px;
    position: absolute;
    top: 50%;
    left: 40px;
    z-index: 0;
    transform: translateY(-50%);
    background-color: @white;
    background-image: url("../img/men-icon.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;

    @media screen and (max-width: @small) {
      top: 20px;
      transform: translateY(0);
    }
  }

  &:nth-child(2) {
    &::after {
      background-image: url("../img/hand-icon.png");
    }
  }

  &:nth-child(3) {
    &::after {
      background-image: url("../img/time-icon.png");
    }
  }
}

.project-info__main-list-bold {
  font-size: 17px;
  line-height: 29px;
}

.project-info__img {
  @media screen and (max-width: @small) {
    width: 100%;
    height: auto;
  }
}
