.capabilities {
  background: url("../img/possibility-bg.jpg");
  background-position: 0 190px;
  background-size: 100% 392px;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 110px;

  @media screen and (max-width: @large) {
    background-size: 100% auto;
    padding-bottom: 70px;
  }

  @media screen and (max-width: @medium) {
    background-position: 0 310px;
    background-size: auto 220px;
  }

  &::after {
    content: "";
    display: block;
    height: 104px;
    width: 50%;
    position: absolute;
    top: 478px;
    right: 0;
    z-index: 0;
    //transform: translateX(62.5%);
    background-color: @white;

    @media screen and (max-width: @extra-large) {
      display: none;
    }
  }
}

.capabilities__title-wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 452px;

  @media screen and (max-width: @large) {
    padding-bottom: 270px;
  }

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.capabilities__title {
  width: 100%;
  max-width: 450px;
}

.capabilities__txt {
  width: 100%;
  max-width: 400px;

  @media screen and (max-width: @large) {
    max-width: 300px;
  }
}

.capabilities__schedule-1-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-left: 50px;
  margin-bottom: 100px;

  @media screen and (max-width: @extra-large) {
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }

  @media screen and (max-width: @small) {
    padding-left: 0;
  }

  .capabilities__txt {
    width: 200px;

    @media screen and (max-width: @extra-large) {
      width: 100%;
      padding-top: 50px;
    }
  }

  .capabilities__info {
    width: 100%;
    max-width: 50%;
    background-color: @white;
    margin-top: -102px;
    padding-top: 75px;
    position: relative;
    z-index: 1;

    @media screen and (max-width: @extra-large) {
      margin-top: 0;
      margin-left: auto;
      margin-right: auto;
    }

    @media screen and (max-width: @large) {
      max-width: 100%;
    }
  }
}

.capabilities__slogan {
  font-size: 17px;
  line-height: 26px;
  font-weight: 500;
  margin-bottom: 15px;
}

.capabilities__img-wrapper {
  margin-bottom: 15px;
  text-align: right;

  @media screen and (max-width: @large) {
    text-align: center;
  }
}

.capabilities__img-1 {
  @media screen and (max-width: @small) {
    width: 100%;
    height: auto;
  }
}

.capabilities__info-list {
  display: flex;
  margin-bottom: 15px;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.capabilities__info-list-item {
  padding-left: 30px;
  padding-right: 15px;
  position: relative;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }

  &::after {
    content: "";
    width: 20px;
    height: 20px;
    display: block;
    background-color: @red;
    position: absolute;
    top: 0;
    left: 0;
  }

  &:nth-child(2) {
    &::after {
      background-color: #293746;
    }
  }

  &:nth-child(3) {
    &::after {
      background-color: @white;
      border-radius: 50%;
      border: 2px solid @red;
    }
  }
}

.capabilities__schedule-2-wrapper {
  .capabilities__wrapper-inner {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: @extra-large) {
      flex-wrap: wrap;
    }

    .capabilities__info {
      width: 100%;
      max-width: 50%;
      background-color: @white;
      position: relative;
      z-index: 1;

      @media screen and (max-width: @extra-large) {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
      }

      @media screen and (max-width: @medium) {
        max-width: 100%;
      }
    }

    .capabilities__img-wrapper {
      text-align: center;
    }
  }
}

.capabilities__schedule-2-img-wrapper {
  margin-bottom: 100px;
}

.capabilities__img-2 {
  @media screen and (max-width: @small) {
    max-width: 100%;
    height: auto;
  }
}

.capabilities__wrapper-txt {
  width: 100%;
  max-width: 570px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media screen and (max-width: @extra-large) {
    margin-left: auto;
    margin-right: auto;
  }
}

.capabilities__txt-row {
  width: 100%;
  margin-bottom: 20px;
}

.capabilities__txt-column {
  width: 45%;
}

.capabilities__introduction-img {
  width: 100%;
  height: auto;
}
