.header {
  color: @white;
  background:
    url("../img/header-bg.jpg") 0 0 100% no-repeat,
    url("../img/header-main-shadow.png") 0 0 100% no-repeat,
    url("../img/header-shadow.png") 0 0 100% no-repeat;
  padding-top: 30px;
  padding-bottom: 270px;

  @media screen and (max-width: @large) {
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @extra-large) {
    flex-wrap: wrap;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
}

.header__logo {
  @media screen and (max-width: @extra-large) {
    order: 1;
    padding-left: 5%;
  }

  @media screen and (max-width: 1100px) {
    padding-left: 3%;
  }

  @media screen and (max-width: 1024px) {
    padding-left: 15px;
  }

  @media screen and (max-width: @large) {
    padding-left: 15px;
  }
}

.header__navigation {
  width: 100%;
  max-width: 700px;

  @media screen and (max-width: @extra-large) {
    //display: none;
    order: 3;
    max-width: 100%;
    margin-top: 20px;
  }
}

.header__navigation-list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #939393;

  @media screen and (max-width: @extra-large) {
    display: flex;
    flex-direction: column;
    width: 220px;
    padding-left: 30px;
    margin-left: -400px;
    border-top: 1px solid @white;
    border-right: 1px solid @white;
    border-bottom: 1px solid @white;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

.header__navigation-list-item--active {
  position: relative;
  font-weight: bold;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: @white;
    position: absolute;
    bottom: -1px;
    left: 0;
  }
}

.header__navigation-list-item {
  padding-bottom: 20px;

  @media screen and (max-width: @extra-large) {
    padding-top: 12px;
    padding-bottom: 13px;
    font-size: 17px;
    line-height: 26px;
  }
}

.header__navigation-link {
  color: @white;
}

.header__title {
  font-size: 65px;
  line-height: 1;
  width: 540px;

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 375px;
    margin-right: auto;
    font-size: 40px;
    margin-bottom: 60px;
  }
}

.header__btn {
  display: inline-block;
  position: relative;
  z-index: 1;
}

.header__info-wrapper {
  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 159px;
    height: 159px;
    background-image: url("../img/15.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    top: 50%;
    transform: translateY(-50%);
    right: 40%;

    @media screen and (max-width: @extra-large) {
      transform: translateY(100%);
      right: 20%;
    }

    @media screen and (max-width: @medium) {
      transform: translateY(100%);
      right: 0;
      z-index: 0;
    }
  }
}

.header__mobile-btn {
  height: 29px;
  width: 30px;
  position: relative;
  top: 12px;
  right: 15px;
  background-color: transparent;
  border: none;
  outline: none;
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
    order: 2;
  }
}

.header__mobile-btn--gamburger {
  animation: scale 0.3s;

  height: 29px;
  width: 30px;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: block;
    width: 80%;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 13px;
    right: 0;
    border-radius: 1px;
  }
}

.header__mobile-btn--close {
  animation: scale2 0.4s;

  &::after {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 13px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @white;
    position: absolute;
    bottom: 13px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: none;
  }
}

.show {
  animation: show 0.4s;
  margin-left: 0px;
}

.hide {
  @media screen and (max-width: @extra-large) {
    animation: hide 0.4s;
    margin-left: -300px;
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale2 {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes show {
  0% {
    margin-left: -300px;
  }

  100% {
    margin-left: 0;
  }
}

@keyframes hide {
  0% {
    margin-left: 0;
  }

  100% {
    margin-left: -300px;
  }
}

