.form {
  display: flex;
  flex-direction: column;
}

.form__input-text {
  font-size: 13px;
  line-height: 22px;
  padding-top: 12px;
  padding-left: 14px;
  padding-right: 14px;
  padding-bottom: 10px;
  border: 2px solid #d7d7d7;
  margin-bottom: 20px;

  &::placeholder {
    font-family: @inter;
  }
}

.form__textarea {
  min-height: 166px;
}

.form__btn {
  line-height: 22px;
  padding-top: 12px;
  padding-left: 14px;
  padding-right: 14px;
  padding-bottom: 10px;
  background-color: @red;
  color: @white;
  border: none;
  font-size: 14px;
  font-weight: 500;
}
